@import url('https://fonts.googleapis.com/css2?family=IM+Fell+English+SC&family=Gowun+Dodum&display=swap');

/* ‚îÄ‚îÄ‚îÄ Ï†ÑÏó≠: ÏÇ¨Ïù¥Ìä∏ Ïä§ÌÅ¨Î°§ Ïû†Í∏à ‚îÄ‚îÄ‚îÄ */
html,
body {
    height: 100%;
    overflow: hidden;
    /* üîí Ï†ÑÏó≠ Ïä§ÌÅ¨Î°§ Ï†úÍ±∞ */
    overscroll-behavior: none;
}

* {
    -webkit-touch-callout: none;
    user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
    box-sizing: border-box;
    /* Ìå®Îî©ÏúºÎ°ú ÎÜíÏù¥ ÎÑòÏπ® Î∞©ÏßÄ */
}

/* ‚îÄ‚îÄ‚îÄ Î∞∞Í≤Ω ÎûòÌçº ‚îÄ‚îÄ‚îÄ */
.codex-wrap {
    height: 100vh;
    /* Î∑∞Ìè¨Ìä∏ ÍΩâ Ï±ÑÏõÄ */
    background: #000;
    display: grid;
    place-items: center;
    padding: 4vh 2vw;
    background-image: repeating-linear-gradient(0deg, #000 0 2px, #0c0c0c 2px 4px);
    animation: bg-noise .25s infinite;
}

@keyframes bg-noise {
    0% {
        background-position: 0 0
    }

    50% {
        background-position: 4px 8px
    }

    100% {
        background-position: -4px -8px
    }
}

/* ‚îÄ‚îÄ‚îÄ CRT ÌîÑÎ†àÏûÑ(ÎÇ¥Î∂Ä Ïä§ÌÅ¨Î°§ Ïª®ÌÖåÏù¥ÎÑà) ‚îÄ‚îÄ‚îÄ */
.crt {
    width: min(960px, 94vw);
    height: min(86vh, 820px);
    /* üîë ÎÜíÏù¥ Ï†úÌïú ‚Üí ÎÇ¥Î∂Ä Ïä§ÌÅ¨Î°§ Ïú†ÎèÑ */
    display: flex;
    flex-direction: column;
    /* ÏúÑ: ÌÉÄÏù¥ÌãÄ, Í∞ÄÏö¥Îç∞: Î≥∏Î¨∏(Ïä§ÌÅ¨Î°§), ÏïÑÎûò: Ïï°ÏÖò */
    overflow: hidden;
    /* ÌîÑÎ†àÏûÑ ÏûêÏ≤¥ Ïä§ÌÅ¨Î°§ Ï†úÍ±∞ */

    background: rgba(10, 10, 10, 0.85);
    border-radius: 12px;
    padding: 18px;
    /* ÏÇ¥Ïßù Ï§ÑÏó¨ ÎÇ¥Î∂Ä Ïó¨Ïú† ÌôïÎ≥¥ */
    border: 1px solid rgba(255, 255, 255, .08);
    box-shadow: inset 0 0 40px rgba(255, 0, 0, .08), 0 0 80px rgba(0, 0, 0, .9);
    animation: flicker 2s infinite;
}

@keyframes flicker {

    0%,
    19%,
    21%,
    23%,
    25%,
    54%,
    56%,
    100% {
        opacity: 1
    }

    20%,
    24%,
    55% {
        opacity: .88
    }
}

/* ‚îÄ‚îÄ‚îÄ ÏÉÅÎã® ÌÉÄÏù¥ÌãÄ(Í≥†Ï†ï) ‚îÄ‚îÄ‚îÄ */
.codex-title {
    flex: 0 0 auto;
    /* Í≥†Ï†ï ÏòÅÏó≠ */
    font-family: 'IM Fell English SC', serif;
    font-size: clamp(1.8rem, 4vw, 2.6rem);
    text-align: center;
    margin: 0 0 12px;
    color: #e45;
    text-shadow: 0 0 10px rgba(255, 0, 0, .4);
}

/* ‚îÄ‚îÄ‚îÄ Î≥∏Î¨∏(Ïó¨Í∏∞Îßå Ïä§ÌÅ¨Î°§) ‚îÄ‚îÄ‚îÄ */
.codex-body {
    flex: 1 1 auto;
    /* ÎÇ®ÏùÄ Í≥µÍ∞Ñ Ï†ÑÎ∂Ä ÏÇ¨Ïö© */
    min-height: 0;
    /* üîë ÎÇ¥Î∂Ä Ïä§ÌÅ¨Î°§ ÌóàÏö© */
    overflow-y: auto;
    /* üî• ÎÇ¥Î∂Ä Ïä§ÌÅ¨Î°§ */
    overflow-x: hidden;
    padding: 8px 8px 12px;
    opacity: 0;
    transform: translateY(12px);
    transition: opacity .6s ease, transform .6s ease;
}

.codex-body.show {
    opacity: 1;
    transform: translateY(0);
}

/* Ï±ïÌÑ∞/Ìï≠Î™© */
.codex-chapter h2 {
    font-family: 'IM Fell English SC', serif;
    color: #cfcfcf;
    font-size: 1.2rem;
    margin: 14px 0 6px;
    text-shadow: 0 0 5px rgba(255, 255, 255, .2);
    padding-top: 10px;
}

.codex-chapter ul {
    margin: 0;
    padding-left: 2.2rem;
}

.codex-chapter li {
    font-family: 'Gowun Dodum', sans-serif;
    color: #aaa;
    font-size: .95rem;
    margin-bottom: 4px;
    position: relative;
}

.codex-chapter li::before {
    content: '‚Ä†';
    position: absolute;
    left: -1rem;
    color: #ff5555;
    text-shadow: 0 0 6px rgba(255, 0, 0, .6);
}

/* ‚îÄ‚îÄ‚îÄ ÌïòÎã® Ïï°ÏÖò(Í≥†Ï†ï) ‚îÄ‚îÄ‚îÄ */
.codex-actions {
    flex: 0 0 auto;
    /* Í≥†Ï†ï ÏòÅÏó≠ */
    display: flex;
    justify-content: space-between;
    gap: 10px;
    margin-top: 10px;
    padding-top: 10px;
    border-top: 1px solid rgba(255, 255, 255, .06);
    background: linear-gradient(180deg, transparent, rgba(0, 0, 0, .35));
}

/* Î≤ÑÌäº */
.btn {
    appearance: none;
    cursor: pointer;
    transition: .2s ease;
    font-family: 'Gowun Dodum', sans-serif;
    border: 1px solid rgba(255, 255, 255, .12);
    padding: 8px 14px;
    border-radius: 8px;
    background: rgba(255, 255, 255, .05);
    color: #ddd;
    box-shadow: inset 0 0 0 1px rgba(255, 255, 255, .02);
}

.btn:hover {
    background: rgba(255, 0, 0, .1);
    border-color: rgba(255, 0, 0, .4);
    color: #fff;
}

.btn.proceed {
    border-color: rgba(0, 255, 170, .25);
    box-shadow: inset 0 0 12px rgba(0, 255, 170, .12), 0 0 10px rgba(0, 255, 170, .08);
}

.btn:active {
    transform: translateY(1px)
}

/* ‚îÄ‚îÄ‚îÄ Ïä§ÌÅ¨Î°§Î∞î ÌÖåÎßà(ÎÇ¥Î∂Ä Î≥∏Î¨∏ÏóêÎßå Ï†ÅÏö©) ‚îÄ‚îÄ‚îÄ */
.codex-body::-webkit-scrollbar {
    width: 10px;
}

.codex-body::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, .55);
    border-left: 1px solid rgba(255, 0, 0, .18);
}

.codex-body::-webkit-scrollbar-thumb {
    background: linear-gradient(180deg, #a00, #600);
    border-radius: 6px;
    box-shadow: 0 0 6px rgba(255, 0, 0, .45);
}

.codex-body::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(180deg, #f00, #800);
}

/* Firefox */
.codex-body {
    scrollbar-color: #7b0a0a rgba(0, 0, 0, .55);
    scrollbar-width: thin;
}