* {
    -webkit-touch-callout: none;
    user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
}

html, body {
    height: auto;
    min-height: 100%;
    overflow-y: auto !important;
    -webkit-touch-callout: auto !important;
    user-select: auto !important;
    -moz-user-select: auto !important;
    -ms-user-select: auto !important;
    -webkit-user-select: auto !important;
}

/* Ï†ÑÏ≤¥ Î∞∞Í≤Ω Î∞è ÌÖçÏä§Ìä∏ Ïä§ÌÉÄÏùº */
.profile-container {
    background-color: #000;
    color: #fff;
    padding: 2rem;
    font-family: 'IM Fell English SC', serif;
    min-height: 100vh;
}

/* ÌÉÄÏù¥ÌãÄ */
.creepy-title {
    font-size: 2.2rem;
    text-align: center;
    color: #ff3c3c;
    margin-bottom: 3rem;
    text-shadow: 0 0 5px #600;
}

/* Í∞Å ÏÜåÏÜçÏÇ¨ Î∏îÎü≠ */
.agency-block {
    margin-bottom: 4rem;
    text-align: center;
}

/* ÏÜåÏÜçÏÇ¨ Ïù¥Î¶Ñ */
.agency-name {
    font-size: 1.4rem;
    color: #fff;
    margin-bottom: 1.5rem;
    text-transform: uppercase;
    text-shadow: 0 0 3px #ff3c3c;
}

/* Ïπ¥Îìú Í∑∏Î¶¨Îìú: Ï§ëÏïô Ï†ïÎ†¨, Î∞òÏùëÌòï */
.card-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1.5rem;

    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
}

/* Í∞Å Ïπ¥Îìú Ïä§ÌÉÄÏùº */
.profile-card {
    background-color: #111111e6;
    /* ÏÇ¥Ïßù Ìà¨Î™ÖÌïú Í≤ÄÏ†ï */
    border: 1px solid #ff4d4d;
    border-radius: 12px;
    padding: 1rem;
    width: 160px;
    height: 120px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    transition: transform 0.2s ease;
    cursor: pointer;
}

.profile-card:hover {
    transform: scale(1.05);
}

/* Ïù¥ÎØ∏ÏßÄ Ïä§ÌÉÄÏùº */
.profile-image {
    width: 100%;
    max-height: 100px;
    object-fit: contain;
    display: block;
    margin-bottom: 0.5rem;
}

/* Ïú†Îãõ Ïù¥Î¶Ñ Ïà®Í∏∞Í∏∞ */
.profile-name {
    display: none;
}

/* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
   Elevator-style Home Button
   ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */
.elev-home-wrap {
    position: sticky;
    bottom: 0;
    display: flex;
    justify-content: center;
    padding: clamp(14px, 3.5vw, 24px) 0;
    background: transparent; /* üî• Î∞∞Í≤Ω Ï†úÍ±∞ */
    z-index: 20;
}

.elev-home-btn {
    position: relative;
    display: inline-flex;
    align-items: center;
    gap: clamp(8px, 2vw, 12px);
    padding: clamp(8px, 2.2vw, 12px) clamp(14px, 3.2vw, 20px);
    border: 1px solid #2a2a2a;
    border-radius: 999px;
    background: radial-gradient(circle at 35% 35%, #2f2f34, #131318 70%);
    cursor: pointer;
    box-shadow:
        0 0 0 1px #141414 inset,
        0 8px 24px rgba(0, 0, 0, .65),
        0 0 26px rgba(161, 26, 26, .18);
    transition: transform .08s ease, filter .15s ease, box-shadow .2s ease;
    outline: none;
}

.elev-home-btn:hover {
    filter: brightness(1.06);
    box-shadow:
        0 0 0 1px #1d1d1d inset,
        0 10px 28px rgba(0, 0, 0, .7),
        0 0 34px rgba(161, 26, 26, .28);
}

.elev-home-btn:active {
    transform: translateY(2px);
    filter: brightness(.96);
}

.elev-home-btn:focus-visible {
    box-shadow:
        0 0 0 2px #a11a1a,
        0 0 0 6px rgba(161, 26, 26, .25);
}

/* Î∂âÏùÄ ÏÉÅÌÉúÎì±(ÏóòÎ¶¨Î≤†Ïù¥ÌÑ∞ Ï†êÎì± ÎäêÎÇå) */
.elev-dot {
    width: clamp(10px, 2.4vw, 14px);
    height: clamp(10px, 2.4vw, 14px);
    border-radius: 50%;
    background: radial-gradient(circle at 40% 40%, #ff4040, #a11a1a);
    box-shadow: 0 0 10px #a11a1a, 0 0 18px rgba(161, 26, 26, .6);
    animation: elevBlink 1.4s infinite;
}

@keyframes elevBlink {

    0%,
    100% {
        opacity: 1;
    }

    50% {
        opacity: .25;
    }
}

/* HOME ÎùºÎ≤® */
.elev-label {
    font-family: 'IM Fell English SC', serif;
    color: #e7e4e4;
    text-shadow: 0 0 6px rgba(161, 26, 26, .35);
    letter-spacing: .12em;
    white-space: nowrap;
    font-size: clamp(14px, 2.8vw, 18px);
}

/* Îã§ÌÅ¨ ÌÖåÎßà Î∞∞Í≤ΩÍ≥º Ïù¥Í≤© */
.profile-container {
    padding-bottom: clamp(60px, 12vw, 110px);
    /* Î≤ÑÌäº ÏòÅÏó≠ ÌôïÎ≥¥ */
}